/* purgecss start ignore */

.lc-stripe-card-input {
  margin-bottom: 24px;
  width: 100%;
}

.lc-stripe-card-input .StripeElement {
    border-width: 1px;
    border-style: solid;
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray-darker), var(--tw-text-opacity));
    font-size: 16px;
    box-sizing: border-box;
    border-radius: 4px;
    background-color: white;
    width: 100%;
    padding: 12px 14px;
    line-height: 1.1876em;
    letter-spacing: 0.00938em;
  }

.lc-stripe-card-input .StripeElement:hover {
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-gray), var(--tw-border-opacity));
    }

.lc-stripe-card-input .StripeElement--focus {
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
      box-shadow: none;
    }

.lc-stripe-card-input .StripeElement--invalid {
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-danger), var(--tw-border-opacity));
    }

.lc-stripe-card-input .StripeElement--webkit-autofill {
      background-color: #fefde5 !important;
    }

.lc-stripe-card-input-label {
    margin-bottom: 8px;
    display: block;
    font-size: 12px;
  }

.lc-stripe-card-input-invalid .StripeElement {
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-danger), var(--tw-border-opacity));
    }

.lc-stripe-card-input-error {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-danger), var(--tw-text-opacity));
    font-size: 0.8em;
    line-height: 1.25;
    font-family: inherit;
    margin-top: 8px;
    margin-left: 14px;
    margin-right: 14px;
  }

/* purgecss end ignore */
